<template>
  <div id="app">
    <div class="cp-console">
  		<div id="output">{{ console }}</div>
  	</div>
		<div class="cp-form">
			<div class="cp-form_item">
				<label for="turnTo" class="cp-form_label">turnTo</label>
				<select class="cp-form_input" v-model="params.turnTo">
          <option value="b">浏览器</option>
          <option value="a">应用商店</option>
        </select>
        <span class="cp-form_tip">a:应用商店，b:浏览器</span>
			</div>
  			<div class="cp-form_item">
  				<label for="run" class="cp-form_label">run</label>
  				<input type="text" class="cp-form_input" v-model="params.run" />
          <span class="cp-form_tip">是否自动打开，1为打开</span>
  			</div>
			<div class="cp-form_item">
				<label for="packageName" class="cp-form_label">packageName</label>
				<input type="text" class="cp-form_input" v-model="params.packageName" />
			</div>
			<div class="cp-form_item">
				<label for="appId" class="cp-form_label">appId</label>
				<input type="text" class="cp-form_input" v-model="params.appId" />
			</div>
			<div class="cp-form_item">
				<label for="versionCode" class="cp-form_label">versionCode</label>
				<input type="text" class="cp-form_input" v-model="params.versionCode" />
			</div>
			<div class="cp-form_item">
				<label for="gameUrl" class="cp-form_label">gameUrl</label>
				<input type="text" class="cp-form_input" v-model="params.gameUrl" />
        <span class="cp-form_tip">第三方页面地址</span>
			</div>
			<div class="cp-form_item">
				<label for="title" class="cp-form_label">title</label>
				<input type="text" class="cp-form_input" v-model="params.title" />
			</div>
			<div class="cp-form_item">
				<label for="scheme" class="cp-form_label">scheme</label>
				<input type="text" class="cp-form_input" v-model="params.scheme" />
			</div>
			<div class="cp-form_item">
				<label for="backgroundColor" class="cp-form_label">backgroundColor</label>
				<input type="text" class="cp-form_input" v-model="params.backgroundColor" />
			</div>
			<div class="cp-form_item">
				<label for="btnColor" class="cp-form_label">btnColor</label>
				<input type="text" class="cp-form_input" v-model="params.btnColor" />
			</div>
			<div class="cp-form_item">
				<label for="textColor" class="cp-form_label">textColor</label>
				<input type="text" class="cp-form_input" v-model="params.textColor" />
			</div>
			<div class="cp-form_item">
				<label for="installText" class="cp-form_label">installText</label>
				<input type="text" class="cp-form_input" v-model="params.installText" />
			</div>
			<div class="cp-form_item">
				<label for="updateText" class="cp-form_label">updateText</label>
				<input type="text" class="cp-form_input" v-model="params.updateText" />
			</div>
			<div class="cp-form_item">
				<label for="installCompleteText" class="cp-form_label">installCompleteText</label>
				<input type="text" class="cp-form_input" v-model="params.installCompleteText" />
			</div>
		</div>
  </div>
</template>

<script>
import base64url from 'base64url'
import hexRgb from 'hex-rgb'
export default {
  name: 'app',
  data () {
    return {
      params: {
        turnTo: 'b',
        run: 1,
        packageName: 'com.Qunar',
        appId: 303048,
        versionCode: 135,
        gameUrl: '',
        title: '页面名字',
        scheme: 'qunaraphone://railway/multipleTrainList?dep=深圳&arr=上海&date=2016-12-01',
        backgroundColor: '',
        btnColor: '',
        textColor: '',
        installText: '安装',
        updateText: '更新',
        installCompleteText: '打开'
      }
    }
  },
  computed: {
    console () {
      let result = 'https://bro-res2.flyme.cn/resources/activity/cp/p-native.html?'
      let params = []
      let gameUrl = this.params.gameUrl && base64url.encode(this.params.gameUrl)
      let scheme = this.params.scheme && base64url.encode(this.params.scheme)
      for (let k in this.params) {
        if (this.params.hasOwnProperty(k) && ['scheme', 'gameUrl'].indexOf(k) == -1) {
          let value = this.params[k]
          if (~k.indexOf('Color') && ~value.indexOf('#')) {
            value = `rgb(${hexRgb(value).join(',')})`
          }
          if (value) {
            params.push(`${k}=${value}`)
          }
        }
      }
      if (gameUrl) {
        params.push('gameUrl=' + gameUrl)
      }
      if (scheme) {
        params.push('scheme=' + scheme)
      }
      result += params.join('&')
      return result
    }
  }
}
</script>

<style>
#app {
  margin: 10px auto;
  width: 800px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  margin-top: 60px;
  font-size: 18px;
}
.cp-console {
  background: #888;
  color: #fff;
  padding: 10px;
  font-size: 16px;
  word-break: break-all;
  margin-bottom: 20px;
}
.cp-form_item {
  margin-bottom: 5px;
}
.cp-form_item label {
  display: inline-block;
  width: 150px;
}
.cp-form_item input,.cp-form_item select,.cp-form_item textarea {
  display: inline-block;
  width: 400px;
  line-height: 24px;
  box-sizing: border-box;
  font-size: 14px;
}
.cp-form_tip {
  font-size: 14px;
}
</style>



